.card-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  grid-gap: 20px;
  gap: 20px;
  padding: 20px;
  color: black;
}

.card {
  background-color: whitesmoke;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.15);
  max-width: 400px;
  padding: 20px;
  margin: 20px;
  overflow: hidden;
}

.card-title {
  font-size: 24px;
  margin: 0;
  margin-bottom: 10px;
}

.card-data {
  font-size: 16px;
  margin: 0;
  margin-bottom: 10px;
}

.sales-sheet-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.sales-sheet-container h3 {
  font-size: 24px;
  margin-bottom: 20px;
}

.sales-sheet-container table {
  width: 100%;
  margin-bottom: 0;
}

.sales-sheet-container th,
.sales-sheet-container td {
  padding: 12px;
  text-align: left;
}

.sales-sheet-container th {
  background-color: #007bff;
  color: #fff;
}

.sales-sheet-container tr:nth-child(even) {
  background-color: #f2f2f2;
}

.sales-sheet-container tr:hover {
  background-color: #ddd;
}

@media (max-width: 768px) {
  .card {
    max-width: 100%;
  }

  .sales-sheet-container {
    padding: 10px;
  }

  .sales-sheet-container th,
  .sales-sheet-container td {
    padding: 8px;
  }
}

/* Container styles */
.sales-sheet-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  background-color: white;
  border-radius: 4px;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
}

/* Form styles */
form {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

form > * {
  flex: 1 1 100%;
  margin-right: 10px;
  margin-bottom: 10px;
}

form > :last-child {
  flex-basis: 100%;
}

/* Input field styles */
input[type="text"] {
  margin-bottom: 10px;
  padding: 8px;
  border-radius: 4px;
  border: 1px solid #333;
  font-size: 16px;
  width: 100%;
  box-sizing: border-box;
}

input[type="text"]:focus {
  border-color: #007bff;
  box-shadow: 0px 0px 3px rgba(0, 123, 255, 0.5);
}

/* Button styles */
button {
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  padding: 8px 16px;
  cursor: pointer;
}

button:hover {
  background-color: #0062cc;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

thead {
  background-color: #007bff;
  color: #0c0c0c;
}

th {
  padding: 10px;
  text-align: left;
  color: #0c0c0c;
}

tr {
  border-bottom: 1px solid #ccc;
}

tr:hover {
  background-color: #f5f5f5;
}

td {
  padding: 10px;
  vertical-align: middle;
}

.HeadNum {
  font-weight: bold;
  font-size: 18px;
}

h3 {
  margin-top: 50px;
  text-align: center;
  font-size: 24px;
}

.table-raw > th {
  color: #0c0c0c;
}

.sales-sheet-card {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}

.sales-sheet-card-item {
  flex-basis: calc(33.33% - 16px);
  margin: 8px;
  padding: 16px;
  border: 1px solid #ddd;
  border-radius: 4px;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
}
.sales-sheet-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: auto;
  grid-gap: 16px;
}

.sales-sheet-grid-item {
  padding: 16px;
  border: 1px solid #ddd;
  border-radius: 4px;
}
.sales-sheet-table {
  border-collapse: collapse;
  width: 100%;
}

.sales-sheet-table th,
.sales-sheet-table td {
  padding: 8px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}

.sales-sheet-table th {
  background-color: #f2f2f2;
}

.row {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  flex-direction: row;
}

.col {
  margin: 10px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  flex-direction: row;
}

.card {
  background-color: black;
  border-radius: 10px;
  box-shadow: 1px 1px 1px 1px limegreen;
  overflow: hidden;
  text-align: center;
  width: 300px;
  color: whitesmoke;
  border: 1px solid gray;
  flex-direction: column;
}

.card img {
  height: 200px;
  object-fit: cover;
  width: 100%;
}

.card-header {
  font-size: 18px;
  font-weight: bold;
  margin: 10px 0;
}

.card-body {
  font-size: 16px;
  margin-bottom: 10px;
}
.search-input {
  padding: 10px;
  border: 2px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
  width: 300px;
  outline: none;
}

.search-input:focus {
  border-color: #007bff;
  box-shadow: 0 0 5px #007bff;
}

